export class LocalConfig{
    basicsConfig:any;
    constructor(){
        this.basicsConfig={
            API_URL:'http://api.qeedagame.com/api',
            INTEK_API_URL:'https://intek.edukitapp.com',
            CDN_URL:'https://hub.iesmaster.org',
        }
    }
    getBasics(){
        return this.basicsConfig;
    }
}


