import { Injectable } from '@angular/core';
import { Headers, Response, Http } from '@angular/http';
import { Subject } from 'rxjs';
import { Observable } from 'rxjs/Observable';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { AppConfig } from '../../app.config';

@Injectable()
export class AuthService {
    authObservable :Subject<any>;

    constructor(private http:HttpClient, private router: Router){
        this.authObservable=new Subject();
     }

    login(username, password) : Observable<any>{
        return this.http.post<any>(AppConfig.BASICS.API_URL+"/auth/signin", {username:username, password:password});
    }

    signup(data) : Observable<any>{
        return this.http.post<any>(AppConfig.BASICS.API_URL+"/auth/signup", data);
    }

    processForgotPass(email) : Observable<any>{
        return this.http.post<any>(AppConfig.BASICS.API_URL+"/auth/forgot-password", {email:email});
    }

    loggedIn(){
        var token=window.localStorage.getItem("authToken");
        var user=window.localStorage.getItem("user");
        if(token&&token!='undefined'&&token!==null&&user&&user!='undefined'&&user!==null){
            return true;
        }
        return false;
    }

    logout(){
        window.localStorage.removeItem("authToken");
        // this.authObservable.next({loggedOut:true});
        //^For use in LoginWindow
        this.router.navigate(["/admin/auth/login"]);
        return true;
    }

    user():any{
        let user=window.localStorage.getItem("user");
        if(user){
            user=JSON.parse(user);
            return user;
        }
        return null;
    }

    admin(){
        let u=window.localStorage.getItem("user");
        if(u){
            var user=JSON.parse(u);
        }
        if(user&&(user.type=="ADMIN"||user.type=="STAFF")){
            return user;
        }
        return null;
    }

    faculty(){
        let u=window.localStorage.getItem("user");
        if(u){
            var user=JSON.parse(u);
        }
        if(user&&user.type=="FACULTY"){
            return user;
        }
        return null;
    }

    student(){
        let u=window.localStorage.getItem("user");
        if(u){
            var user=JSON.parse(u);
        }
        if(user&&user.type=="STUDENT"){
            return user;
        }
        return null;
    }

    staff(){
        let u=window.localStorage.getItem("user");
        if(u){
            var user=JSON.parse(u);
        }
        if(user&&user.type=="STAFF"){
            return user;
        }
        return null;
    }

    getAuthToken(){
        return window.localStorage.getItem("authToken");
    }
    getDefaultSession(){
        return 'null';
    }
    getDefaultCenter(){
        return 'null';
    }
    getUserPerms(userPemrs, app, activity, reqPerms){
        let perms:any={};
        if(!userPemrs){
            return perms;
        }
        if(userPemrs[0]=="ALL"||userPemrs.all){
            for(let i=0; i<reqPerms.length; i++){
                perms[reqPerms[i]]=true;
            }
        }
        if(!userPemrs[app]){
            return perms;
        }
        if(!userPemrs[app][activity]){
            return perms;
        }
        for(let i=0; i<reqPerms.length; i++){
            if(userPemrs[app][activity][reqPerms[i]]){
                perms[reqPerms[i]]=true;
            }
        }
        return perms;
    }

    getDefaultParams(){
        let u=window.localStorage.getItem("user");
        if(u){
            let user=JSON.parse(u);
            return user.defaultParams;
        }else{
            return null;
        }
    }
}


